<template>
    <table cellspacing="0" cellpadding="0" border="0" class="cl-table-head" :style="tableHeadStyle">
        <colgroup>
            <col v-for="column in colgroupColumns" :key="column.__id" :width="setColWidth(column)">
            <col v-if="$parent.showVerticalScrollBar" :width="$parent.scrollBarWidth">
        </colgroup>
        <thead>
            <template v-for="(row, index) in columns">
                <cl-table-tr type="head" :row="row[0]" :key="index">
                    <th v-for="column in row"
                        :key="column.__id"
                        :class="[
                            fixed && column.fixed !== fixed && 'is-hidden',
                            sortType.includes(column.__sort) && column.__sort && 'cl-table-head__sort',
                            column.className
                        ]"
                        :rowspan="column.__rowSpan"
                        :colspan="column.__colSpan"
                        @click.stop="sortHandle(column)">
                        <cl-table-head-cell :column="column" :sortType="sortType"></cl-table-head-cell>
                    </th>
                    <th class="cl-table-head__scroll-th" v-if="$parent.showVerticalScrollBar" :rowspan="columns.length"></th>
                </cl-table-tr>
            </template>
        </thead>
    </table>
</template>

<script>
    import ClTableTr from './table-tr.vue'
    import ClTableHeadCell from './table-head-cell'
    import tableMixins from './table-mixins'

    export default {
        name: "ClTableHead",
        mixins: [tableMixins],
        inject: ['tableRoot'],
        props: {
            data: {
                type: Array,
                default: function () {
                    return []
                }
            },
            columns: {
                type: Array,
                default: function () {
                    return []
                }
            },
            colgroupColumns: Array,
            columnsWidth: Object,
            headStyle: Object,
            fixed: String
        },
        data() {
            return {
                sortType: ['ascend', 'descend', true, false, 'remote']
            }
        },
        computed: {
            tableHeadStyle(){
                let style = {};
                if(this.headStyle && this.headStyle.width) style.width = this.headStyle.width
                return style;
            }
        },
        components: {
            ClTableTr,
            ClTableHeadCell
        },
        created() {
        },
        mounted() {
        },
        methods: {
            sortHandle(column){
                this.tableRoot.headClick(column);
                if(!column.__sort || !this.sortType.includes(column.sort)) return;
                let type = true;
                if(column.__sort === 'ascend'){
                    type = 'descend';
                }else if(column.__sort === 'descend'){
                    type = true;
                }else if(column.__sort === true){
                    type = 'ascend';
                }else{
                    type = 'remote'
                }
                this.tableRoot.sortHandle(column, type);
            }
        }
    }
</script>
